<template>
  <div id="blogDetails">
	<div class="details container">
		<router-link class="btn btn-default" to="/">返回</router-link>
		<h1 class="page-header">{{blog.title}}</h1>
		<ul class="list-group">
			<li class="list-group-item">{{blog.body}}</li>
		</ul>
	</div>
  </div>
</template>

<script>

export default {
  name: 'blogDetails',
  data(){
	return{
		id:this.$route.params.id,
		blog:{}
	}
  },
  created(){
	this.$http.get("https://jsonplaceholder.typicode.com/posts/" + this.id)
	.then(function(data){
		// console.log(data);
		this.blog = data.body;
	})
  }
}
</script>

