<template>
  <div class="add-blog">
    <h2>添加博客</h2>
	<form v-if="!submmited">
		<label>博客标题</label>
		<input type="text" v-model="blog.title" required="" />
		<label>博客内容</label>
		<textarea v-model="blog.content" style="resize: none;"></textarea>
		
		<div id="checkboxs">
			<lable>Vue.js</lable>
			<input type="checkbox" value="Vue.js" v-model="blog.blogtypes"/>
			<lable>Node.js</lable>
			<input type="checkbox" value="Node.js" v-model="blog.blogtypes"/>
			<lable>React.js</lable>
			<input type="checkbox" value="React.js" v-model="blog.blogtypes"/>
			<lable>Anguale4</lable>
			<input type="checkbox" value="Anguale4" v-model="blog.blogtypes"/>
		</div>
		<label>作者</label>
		<select v-model="blog.author">
			<option v-for="author in authors" :key="author">{{author}}</option>
		</select>
		<button v-on:click.prevent="post">添加博客</button>
	</form>
	
	<div v-if="submmited">
		<h3>您的博客发布成功!</h3>
	</div>
	
	<div id="preview">
		<h3>博客总览</h3>
		<p>博客标题:{{blog.title}}</p>
		<p>博客内容:</p>
		<p>{{blog.content}}</p>
		<p>博客分类</p>
		<ul>
			<li v-for="blogtype in blog.blogtypes" :key="blogtype">
				{{blogtype}}
			</li>
		</ul>
		<p>作者:{{blog.author}}</p>
	</div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data(){
	return {
		blog:{
			title:"",
			concent:"",
			blogtypes:[]
		},
		authors:['python','java','php','c/c++','c#'],
		submmited:false
	}
  },
  methods:{
	post:function(){
		if(!this.blog.title & !this.blog.content){
			alert("请添加博客标题及内容信息!");
		}else{
			this.$http.post("https://jsonplaceholder.typicode.com/posts",{
				title:this.blog.title,
				body:this.blog.content,
				userId:1
			})
			.then(function(data){
				console.log(data);
				this.submmited = true;
			})
		}
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scoped">
.add-blog *{
	box-sizing: border-box;
}
.add-blog{
	margin: 0 auto;
	max-width: 1000px;
	padding: 20px;
}
label{
	display: block;
	margin: 20px 0 10px;
}
input[type="text"],textarea,select{
	display: block;
	width: 100%;
	padding: 8px;
}
#checkboxs label{
	display: inline-block;
	margin-top: 0;
}
#checkboxs input{
	display: inline-block;
	margin-right: 10px;
}
button{
	display: block;
	margin: 20px 0;
	background: crimson;
	color: white;
	border: 0;
	padding: 14px;
	border-radius: 4px;
	font-size: 18px;
	cursor: pointer;
}
#preview{
	padding: 10px 20px;
	border: 1px dotted #ccc;
	margin: 30px 0;
}
h3{
	margin-top: 10px;
}
</style>