<template>
  <div class="showblogs" v-blogebackgrounds:column="'wide'">
	<h1>博客总览</h1>
	<input type="text" class="form-control" v-model="search" placeholder="搜索" />
	<div class="single-blog" v-for="blog in filteredBlogs" :key="blog.index">
		<router-link v-bind:to="'/details/' + blog.id">
			<h2 v-rancolor>{{blog.title | to-uppercase}}</h2>
		</router-link>
		<article>{{blog.body | snippet}}</article>
	</div>
  </div>

</template>

<script>
export default {
  name: 'showblogs',
  data(){
	return {
		blogs:[],
		search:""
	}
  },
  created(){
	// this.$http.get("/datas.json")  //请求本地文件
	this.$http.get("https://jsonplaceholder.typicode.com/posts")
	.then(function(data){
		// console.log(data);
		this.blogs = data.body.slice(0,10);
	})
  },
  computed:{
	filteredBlogs:function(){
		return this.blogs.filter((blog)=>{
			return blog.title.match(this.search);
		})
	}
  },
  //局部过滤器
  filters:{
	"to-uppercase":function(value){
		return value.toUpperCase();
	},
	// toUpperCase(value){//两种方法效果一致
	// 	return value.toUpperCase();
	// },
	"snippet":function(value){
		return value.slice(0,100) + "...";
	}
  },
  directives:{
	'rancolor':{
		inserted: function (el){
			el.style.color = "#" + Math.random().toString(12).slice(2,8);
		}
	},
	'blogebackgrounds':{
		bind: function (el, binding) {
			if(binding.value == 'wide'){
				el.style.maxWidth = "1000px";
			}else if(binding.value == 'narrow'){
				el.style.maxWidth = "560px";
			}
			if(binding.arg == 'column'){
				el.style.background = "#6677cc";
				el.style.padding = "20px";
			}
		}
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.showblogs{
	max-width: 800px;
	margin: 0 auto;
}
.single-blog{
	padding: 20px;
	margin: 20px 0;
	/* box-sizing: border-box; */
	background-color: #eee;
}
</style>
